
@import './coin.css';

* {
    padding:0;
    margin:0;
    box-sizing: border-box;
    touch-action: manipulation;
}



:root {
    --size-two: 108px;
    --border-radius:10px;

    --dot-size-2:18px;
    --counter-color:#f2b1b5;
    font-size: 16px;
}

body {
    background-color: #eb5661fc;
    margin-top: auto;
    
}

nav {
    position: fixed;
    right: 10px;
    top: 10px;
   
}
.bar {
    background-color:white;
    width: 50px;
    height: 7px;
    padding: 0px;
    margin: 5px;
   
}

.container {
   
    display:grid;
    gap:15px;
    width:100%;
    max-width: 500px;
    justify-items: center;
    grid-template-columns: repeat(3,1fr)

}

.dice-game {
   
    width: 100%;
}

.center {
    margin-left: auto;
    margin-right: auto;
  
}

.dice {
 

    padding: 3px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(3,1fr);

    background-color: white;
    width: var(--size-two);
    height: var(--size-two);
    border-radius: var(--border-radius);

    grid-template-areas:
    "a . b"
    "c d e"
    "f . g" ;
}

.dot {
    justify-self: center;
    align-self: center;
    background-color: black;
    width: var(--dot-size-2);
    height: var(--dot-size-2);
    border-radius: 50%;
}

.counter {

    font-family: Arial, Helvetica, sans-serif ;
    color:var(--counter-color);
    margin-top: 100px;
  
    text-align: center;
    font-size: 10rem;
    width: 40%;

}

.wrapper {
    display:flex;
    flex-direction: row;
    justify-content: center;
    /*height: 100vh;*/
}

.side-menu {
   
    flex-grow: 1;
    background-color: coral;
    padding: 20px;
    display: none;
    transition: all 0.05s ease-in-out;
    
}

.side-container {
    display: flex;
    flex-direction: column;
    margin-top:15%;
}

#toss {
    font-family: Lobster, 'Times New Roman', Times, serif;
    text-align: center;
    font-size: 4.5rem;
    color: cornsilk;
    margin-top: 10px;
    margin-bottom: 10px;
}

input {
    margin-top:15px;
    margin-bottom:10px;
}

input[type=radio] {
    display: none;
}

label {
    margin: 20px;
    color:cornsilk;
    font-size: 2rem;
    font-family: monospace, Arial, Helvetica, sans-serif;
}

label:hover {
    color:rgb(231, 224, 196);
    font-size: 2.1rem;
}



.github-link {
    font-size: 1.3rem;
    font-family: monospace, Arial, Helvetica, sans-serif;
    text-align: center;
    width: 100%;
    
   
}

#github-mark {
    width: 44px;
    height: 44px;
    position: relative;
    opacity: 0.7;
  
}

#github-mark:hover {
   
    opacity: 1;
  
}

@media (max-width:600px) {
    .container {
        width:100%;
    }
    .counter {
        margin-top: 0;
    }

    .wrapper {
        flex-direction: column;
    }

    
    .game-area{
        order:2;      
        
    }
    
    .main-block {
        margin-bottom: 25vh;
        margin-top: 15vh;
    }
  
    .counter{
        margin-top: 25vh;
    }

    #github-mark {
        opacity: 1;
    }

}

@media (min-width:600px) {
    .counter {
        margin-bottom: 50px;
    }

    .game-area {
        flex-basis: 80%;
    }

    .main-block {
        margin-top: 30vh;
    }

    .wrapper {
        height:100vh;
    }

    .github-link {
        margin-top: 25%;
    }


}

#dices{
    margin: 15px;
    font-family: monospace, Arial, Helvetica, sans-serif;
    color: brown;
    font-size: 20px;
    border: 3px solid brown;
    box-shadow: 0 0 20px brown;
    border-radius: 5px;
    border-style:solid;
}

.game-choice{
    text-align: center;
    margin-top: 35px;
    margin-bottom: 25px;
}

#dice{
    margin:-10px;
    width: 20px;
    height: 20px;
}

#coin{
   margin:-10px;
    width: 20px;
    height: 20px;
}